<template>
  <div class="progress">
    <div
      :style="{ height: width + 'px', width: width + 'px' }"
      class="progress_circle"
      v-if="type === 'circle'"
    >
      <svg viewBox="0 0 100 100">
        <path
          :d="getPathD"
          fill="none"
          :stroke="strokeBgColor"
          :stroke-width="relativeStrokeWidth"
          :style="bgPathStyle"
        />
        <path
          :d="getPathD"
          fill="none"
          :stroke="stroke"
          :stroke-linecap="strokeLinecap"
          transform="rotate(90,50,50)"
          :stroke-width="relativeStrokeWidth"
          :style="circlePathStyle"
        />
      </svg>
      <div
        class="circle_text"
        :style="{ height: width + 'px', width: width + 'px' }"
      >
        <slot></slot>
      </div>
    </div>
    <div class="progress_line" v-else>
      <div class="progress_line_wrapper">
        <div class="line_outer" :style="lineOuterStyle">
          <span
            class="line_inner_text"
            :style="{
              lineHeight: `${strokeWidth}px`,
              left: `${strokeWidth / 5}px`
            }"
            v-show="percentage === 0"
            v-if="textInside"
          >
            {{ percentage }}%
          </span>
        </div>
        <div class="line_inner" :style="lineInnerStyle">
          <span
            class="line_inner_text"
            :style="{
              lineHeight: `${strokeWidth}px`,
              right: `${strokeWidth / 5}px`
            }"
            v-if="textInside"
            >{{ percentage }}%
          </span>
        </div>
      </div>
      <div style="margin-left: 10px;">
        <slot></slot>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "mrProgress",
  props: {
    // circle line
    type: {
      type: String,
      default: "line",
      validator: val => ["circle", "line"].indexOf(val) > -1
    },
    //  px
    width: {
      type: Number,
      default: 300
    },
    //  px
    strokeWidth: {
      type: Number,
      default: 20
    },
    // 
    strokeColor: {
      type: [String, Array],
      default: "#6f7ad3"
    },
    // 
    strokeBgColor: {
      type: String,
      default: "#e5e9f2"
    },
    // 
    percentage: {
      type: Number,
      default: 0
    },
    // 
    // round square
    // svg stroke-linecap
    strokeLinecap: {
      type: String,
      default: "round",
      validator: val => ["round", "butt"].indexOf(val) > -1
    },
    // 
    textInside: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    // line
    lineOuterStyle() {
      return {
        width: `${this.width}px`,
        backgroundColor: this.strokeBgColor,
        borderRadius: this.getLineRadius,
        height: `${this.strokeWidth}px`
      };
    },
    // line
    lineInnerStyle() {
      return {
        backgroundColor: this.stroke,
        borderRadius: this.getLineRadius,
        height: `${this.strokeWidth}px`,
        width: `${(this.width / 100) * this.percentage}px`,
        transition: "width 0.6s ease 0s, background-color 0.6s ease"
      };
    },
    getLineRadius() {
      const linecap = this.strokeLinecap;
      return linecap === "round" ? "100px" : "0px";
    },
    isStrokeColorString() {
      return typeof this.strokeColor === "string" ? true : false;
    },
    // 
    stroke() {
      if (this.percentage === 0) return this.strokeBgColor;
      if (this.isStrokeColorString) {
        return this.strokeColor;
      } else {
        return this.getCurrentColor(this.percentage);
      }
    },
    // 
    getPathD() {
      const radius = this.radius;
      return `
        M 50 50
        m -${radius} 0
        a ${radius} ${radius} 0 1 0 ${radius * 2} 0
        a ${radius} ${radius} 0 1 0 -${radius * 2} 0
      `;
    },
    // 
    radius() {
      return this.type === "circle"
        ? parseInt(50 - parseFloat(this.relativeStrokeWidth) / 2, 10)
        : 0;
    },
    // 
    perimeter() {
      return 2 * Math.PI * this.radius;
    },
    // 
    relativeStrokeWidth() {
      return ((this.strokeWidth / this.width) * 100).toFixed(1);
    },
    // 
    strokeDashoffset() {
      const offset = -this.perimeter * 2;
      return `${offset}px`;
    },
    // 
    bgPathStyle() {
      return {
        strokeDasharray: `${this.perimeter}px, ${this.perimeter}px`,
        strokeDashoffset: this.strokeDashoffset
      };
    },
    // 
    // strokeDasharray: 'x, y' x: y:
    circlePathStyle() {
      return {
        strokeDasharray: `${this.perimeter * (this.percentage / 100)}px, ${
          this.perimeter
        }px`,
        strokeDashoffset: this.strokeDashoffset,
        transition: "stroke-dasharray 0.6s ease 0s, stroke 0.6s ease"
      };
    }
  },
  methods: {
    getCurrentColor(percentage) {
      if (typeof this.strokeColor === "string") {
        return this.strokeColor;
      } else {
        return this.getLevelColor(percentage);
      }
    },
    getLevelColor(percentage) {
      // 
      const strokeColor = [...this.strokeColor];
      const colorArray = strokeColor.sort(
        (a, b) => a.percentage - b.percentage
      );
      for (let i = 0; i < colorArray.length; i++) {
        if (colorArray[i].percentage > percentage) {
          return colorArray[i].color;
        }
      }
      return colorArray[colorArray.length - 1].color;
    }
  }
};
</script>

<style scoped>
.progress {
  padding: 5px 0;
  box-sizing: border-box;
}
.progress_circle {
  position: relative;
}
.circle_text {
  position: absolute;
  left: 0;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
.progress_line {
  display: flex;
  align-items: center;
}
.progress_line_wrapper {
  position: relative;
}

.line_inner {
  position: absolute;
  left: 0;
  top: 0;
}
.line_outer {
  position: relative;
}
.line_inner_text {
  position: absolute;
  color: #ffffff;
  font-size: 14px;
}
</style>
